html, body{
    color: white;
}

::-webkit-scrollbar{
    background: transparent;
}
::-webkit-scrollbar-thumb:horizontal{
    background-color: rgba(0,0,0,0.61);
    border-radius: 10px;
}

.app{
    color: white;
    font-family: Maximum impact;
    display: flex;
    flex-direction: column;
    background-color: black;
    height: 100vh;
    overflow: hidden;
}

.header{
    flex: 1;
}

.show_details{
    flex: 2
}

.recommendations{
    flex: 1;
    text-align: center;
}
