.icon{
    position: absolute;
    margin: 22px 0 0 15px;
}

.input{
    border-radius: 30px;
    background-color: #d9d9d96e;
    height: 30px;
    font-weight: 1;
    font-size: 18px;
    width: 100%;
    max-width: 300px;
    border-color: transparent;
    padding: 18px 18px 18px 50px;
    margin: 15px auto;
}
.input::placeholder{
    color: rgba(200, 200, 200, 1)
}
.input:focus{
    outline-width: 0;
    border: solid black 1px
}