.list{
    position: relative;
}

.img{
    border-radius: 1em;
    cursor: pointer;
}

.title{
    position: absolute;
    margin-top: -40px;
    height: 40px;
    padding: 8px 15px 0 15px;
    background-color: rgba(0,0,0,0.6);
    border-bottom-left-radius: 1em;
    border-top-right-radius: 1em;
    text-overflow: ellipsis;
    white-space: nowrap;
    cursor: pointer;
}
